.column{	
	display: flex;
	flex-direction: column;
	/*flex-wrap: wrap;*/
}

.row{	
	display: flex;
	flex-direction: row;
	/*flex-wrap: wrap;*/
}

.wrap{
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
}
.body{
	position:fixed;
  	left: 0px;
  	top: 0px;
  	z-index: -3;
  	width:100%;
  	height:100%;
}
.TopGrad{
	background: rgb(13,46,233);
	background: linear-gradient(90deg, rgba(13,46,233,1) 0%, rgba(12,9,121,1) 0%, rgba(255,198,0,1) 100%);
	position: absolute;
  	left: 0px;
  	top: 0px;
  	z-index: -1;
	width:100%;
	height:auto;
	min-height:400px;
}
.SideGrad{
	background: rgb(13,46,233);
	background: linear-gradient(180deg, rgba(13,46,233,1) 0%, rgba(85,84,84,1) 0%, rgba(255,255,255,1) 100%);
	position:fixed;
  	left: 0px;
  	top: 0px;
	z-index:-2;
	width:auto;
	min-width:50%; 
	maxWidth:50%;
	height:100%;
}


